.modal-custom__dialog {
  position: relative;
  width: auto;
  display: flex;
  margin: 0 auto;
  flex-direction: column;
  background-clip: padding-box;
  border-radius: 5px;
  outline: 0;
}
.modal-custom__close {
  position: absolute;
  right: 10px;
  top: 5px;
  cursor: pointer;
  z-index: 10;
}
.modal-custom__img {
  color: red;
  font-size: 34px;
}
.modal-custom__content {
  display: inline-block;
  padding: 10px;
  width: 400px;
}

:root {
  --animation: glitch; /* glitch, hue, fade, shrink */
  --speed: 2s;
  --easing: linear;
}

.error-indicator {
  padding: 1em;
  font-family: "Press Start 2P", Arial, sans-serif;
  text-transform: uppercase;
  text-align: center;
  color: #2f99ff;
  text-shadow: 0px 2px #255fcc;
  -webkit-animation: glitch 2s infinite linear;
          animation: glitch 2s infinite linear;
  -webkit-animation: var(--animation) var(--speed) infinite var(--easing);
          animation: var(--animation) var(--speed) infinite var(--easing);
}

.error-indicator__title {
  font-size: 3em
}

@-webkit-keyframes glitch {
  0%, 12%, 15%, 52%, 55%, 82%, 85%, 100% { opacity: 1; -webkit-transform: scaleX(1) scaleY(1); transform: scaleX(1) scaleY(1) }
  13% { opacity: .8; -webkit-transform: scaleY(2); transform: scaleY(2); color: #0ff }
  53% { opacity: .8; -webkit-transform: scaleX(.7); transform: scaleX(.7);  color: #f0f }
  83% { opacity: .8; -webkit-transform: rotate(-10deg); transform: rotate(-10deg); color: #ff0 }
}

@keyframes glitch {
  0%, 12%, 15%, 52%, 55%, 82%, 85%, 100% { opacity: 1; -webkit-transform: scaleX(1) scaleY(1); transform: scaleX(1) scaleY(1) }
  13% { opacity: .8; -webkit-transform: scaleY(2); transform: scaleY(2); color: #0ff }
  53% { opacity: .8; -webkit-transform: scaleX(.7); transform: scaleX(.7);  color: #f0f }
  83% { opacity: .8; -webkit-transform: rotate(-10deg); transform: rotate(-10deg); color: #ff0 }
}

@-webkit-keyframes hue {
  13% { color: #0ff }
  53% { color: #f0f }
  83% { color: #ff0 }
}

@keyframes hue {
  13% { color: #0ff }
  53% { color: #f0f }
  83% { color: #ff0 }
}

@-webkit-keyframes shrink {
  0% { -webkit-transform: scaleY(1); transform: scaleY(1) }
  50% { -webkit-transform: scaleY(.5); transform: scaleY(.5) }
  100% { -webkit-transform: scaleY(1); transform: scaleY(1) }
}

@keyframes shrink {
  0% { -webkit-transform: scaleY(1); transform: scaleY(1) }
  50% { -webkit-transform: scaleY(.5); transform: scaleY(.5) }
  100% { -webkit-transform: scaleY(1); transform: scaleY(1) }
}

@-webkit-keyframes fade {
  0% { opacity: 1 }
  50% { opacity: 0 }
  100% { opacity: 1 }
}

@keyframes fade {
  0% { opacity: 1 }
  50% { opacity: 0 }
  100% { opacity: 1 }
}
.cssload-loader {
  position: relative;
  left: calc(50% - 31px);
  width: 100px;
  height: 100px;
  margin: 130px 0;
  -webkit-perspective: 780px;
          perspective: 780px;
}

.cssload-inner {
  position: absolute;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  border-radius: 50%;
}

.cssload-inner.cssload-one {
   left: 0%;
   top: 0%;
   -webkit-animation: cssload-rotate-one 1.15s linear infinite;
           animation: cssload-rotate-one 1.15s linear infinite;
   border-bottom: 3px solid #5C5EDC;
 }
.cssload-inner.cssload-two {
   right: 0%;
   top: 0%;
   -webkit-animation: cssload-rotate-two 1.15s linear infinite;
           animation: cssload-rotate-two 1.15s linear infinite;
   border-right: 3px solid rgba(76, 70, 101, 0.99);
 }
.cssload-inner.cssload-three {
   right: 0%;
   bottom: 0%;
   -webkit-animation: cssload-rotate-three 1.15s linear infinite;
           animation: cssload-rotate-three 1.15s linear infinite;
   border-top: 3px solid rgb(233, 144, 138);
}

@keyframes cssload-rotate-one {
  0% {
    -webkit-transform: rotateX(35deg) rotateY(-45deg) rotateZ(0deg);
            transform: rotateX(35deg) rotateY(-45deg) rotateZ(0deg);
  }
  100% {
    -webkit-transform: rotateX(35deg) rotateY(-45deg) rotateZ(360deg);
            transform: rotateX(35deg) rotateY(-45deg) rotateZ(360deg);
  }
}

@-webkit-keyframes cssload-rotate-one {
  0% {
    -webkit-transform: rotateX(35deg) rotateY(-45deg) rotateZ(0deg);
  }
  100% {
    -webkit-transform: rotateX(35deg) rotateY(-45deg) rotateZ(360deg);
  }
}

@keyframes cssload-rotate-two {
  0% {
    -webkit-transform: rotateX(50deg) rotateY(10deg) rotateZ(0deg);
            transform: rotateX(50deg) rotateY(10deg) rotateZ(0deg);
  }
  100% {
    -webkit-transform: rotateX(50deg) rotateY(10deg) rotateZ(360deg);
            transform: rotateX(50deg) rotateY(10deg) rotateZ(360deg);
  }
}

@-webkit-keyframes cssload-rotate-two {
  0% {
    -webkit-transform: rotateX(50deg) rotateY(10deg) rotateZ(0deg);
  }
  100% {
    -webkit-transform: rotateX(50deg) rotateY(10deg) rotateZ(360deg);
  }
}

@keyframes cssload-rotate-three {
  0% {
    -webkit-transform: rotateX(35deg) rotateY(55deg) rotateZ(0deg);
            transform: rotateX(35deg) rotateY(55deg) rotateZ(0deg);
  }
  100% {
    -webkit-transform: rotateX(35deg) rotateY(55deg) rotateZ(360deg);
            transform: rotateX(35deg) rotateY(55deg) rotateZ(360deg);
  }
}

@-webkit-keyframes cssload-rotate-three {
  0% {
    -webkit-transform: rotateX(35deg) rotateY(55deg) rotateZ(0deg);
  }
  100% {
    -webkit-transform: rotateX(35deg) rotateY(55deg) rotateZ(360deg);
  }
}
.book-list__item {
  display: flex;
  height: calc(100% - 30px);
  margin: 15px 0;
  padding-right: 15px;
}

.book-list__item .book-list__img-wrap {
  flex-shrink: 0;
  width: 150px;
  margin: 0;
}

.book-list__item .book-list__img {
  display: block;
  max-width: 100%;
}

.book-list__item .book-list__content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-bottom: 10px;
}

.book-list__item .book-list__detail {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}

.book-list__item .book-list__title {
  font-size: 1.2rem;
}

.book-list__item .book-list__text {
  font-size: 1rem;
}

.book-list__item .book-list__price {
  font-size: 1.4rem;
}

.book-list__item .book-list__to-cart {

}

.book-list {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  padding: 0;
}

.book-list__card {
  flex: calc(33% - 10px) 1;

  background-clip: content-box;
  margin: 5px;
  list-style: none;
  border: 1px solid #d1d1d1;
  border-radius: 10px;
}
.header-shop__logo,
.header-shop__cart {
  font-size: 20px;
  color: gray;
  cursor: pointer;
  -webkit-transition: .5s all ease;
  transition: .5s all ease;
}

.header-shop__logo:hover,
.header-shop__cart:hover {
  color: #000;
  text-decoration: none;
}

.header-shop__cart-icon {
  margin-right: 10px;
  color: orange;
}


.modal-custom .btn {
  margin: 0 2px;
}
.modal-custom td {
  font-size: 18px;
}
.modal-custom th {
  border-top: 1px solid transparent;
}
.modal-custom .modal__total {
  font-weight: bold;
}
.modal__wrap-btn {
  display: flex;
  justify-content: space-between;
  width: 125px;
}

.modal__empty {
  display: inline-block;
  margin: 20px 30%;
  font-size: 20px;
}

